.flex-containerrr {
    display: flex;
    flex-wrap:wrap;
    justify-content: center;
  }

  .flex-containerrr div{
    margin:0 7px;
  }
  
  .member-name{
    text-align: center;
    color: white;
    font-size: 23px;
    font-weight: 800;
    margin-top: -20px;
    border: 2px solid white;
    background: black;
    border-radius: 0 0 8px 8px;
    padding: 8px;
    width: 300px;
    margin-left: auto;
    margin-right: auto;
    text-transform: uppercase;
    margin-bottom: 40px;
  }

  .member-name-mng{
    text-align: center;
    color: white;
    font-size: 18px;
    font-weight: 800;
    margin-top: -30px;
    border: 2px solid white;
    background: black;
    border-radius: 0 0 8px 8px;
    padding: 8px;
    width: 250px;
    margin-left: auto;
    margin-right: auto;
    text-transform: uppercase;
    margin-bottom: 40px;
  }

  .member-name-exec{
    text-align: center;
    color: white;
    font-size: 18px;
    font-weight: 800;
    margin-top: -30px;
    border: 2px solid white;
    background: black;
    border-radius: 0 0 8px 8px;
    padding: 8px;
    width: 250px;
    margin-left: auto;
    margin-right: auto;
    text-transform: uppercase;
    margin-bottom: 40px;
  }

  .position{
    position: relative;
    top: -50px;
    text-align: center;
    margin-top: 50px;
    color: #7C4295;
    background: white;
    
    padding: 10px;
    font-weight: 800;
    font-size: 30px;
    letter-spacing: 1px;
    width: 400px;
    margin-right: auto;
    margin-left: auto;

  }

  .position-exec/*managers,hc's*/{
    position: relative;
    top: -50px;
    text-align: center;
    margin-top: 50px;
    color: #7C4295;
    background: white;
    border: 1px dotted black;
    padding: 10px;
    font-weight: 800 !important;
    font-size: 22px;
    letter-spacing: 1px;
    width: 400px !important;
    margin-right: auto;
    margin-left: auto;


  }

  @media(max-width:450px){
    .position{
      font-size: 25px;
      width: 350px;
    }
  }

  @media(max-width:350px){
    .position{
      font-size: 25px;
      width: 300px;
    }
  }

  @media(max-width:430px){
    .position-exec{
      width: 350px !important;
    }
  }

  @media(max-width:350px){
    .position-exec{
      
      width: 300px;
    }
  }



